.audios {
  flex: 2;
  margin: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.status {
  margin-bottom: 3rem;
}

.status input {
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  border: none;
  width: 50%;
  padding: 0.5rem;
  border-radius: 2rem;
}

.card {
  display: flex;
  background: linear-gradient(to left top, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.5));
  border-radius: 1rem;
  margin: 2rem 0rem;
  padding: 2rem;
  box-shadow: 6px 6px 20px rgba(122, 122, 122, 0.212);
  justify-content: space-between;
}

.progress {
  background: linear-gradient(to right top, #65dfc9, #6cdbeb);
  width: 100%;
  height: 25%;
  border-radius: 1rem;
  position: relative;
  overflow: hidden;
}
.progress::after {
  content: "";
  width: 100%;
  height: 100%;
  background: rgb(236, 236, 236);
  position: absolute;
  left: 60%;
}
.card-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.percentage {
  font-weight: bold;
  background: linear-gradient(to right top, #65dfc9, #6cdbeb);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.card-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition:
    z-index 0.3s step-end,
    top 0ms ease-in-out 300ms;
  z-index: -1000;
}

.card-modal .overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  border-radius: var(--box-border-radius);
  opacity: 0;
  transition: opacity 100ms ease-in-out 200ms;
}

.card-modal .card-modal-content {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.15);
  width: 95%;
  max-width: 700px;
  background: #fff;
  padding: 25px;
  opacity: 0;
  border-radius: 20px;
  box-shadow: 0 2px 2px 5px rgba(0, 0, 0, 0.05);
  transition: all 300ms ease-in-out;
}

.card-modal .card-modal-content h2 {
  margin: 10px 0;
  font-size: 25px;
  color: var(--green);
  text-align: center;
}

.card-modal .card-modal-content p {
  margin: 15px 0;
  font-size: 16px;
  color: var(--darkblue);
}

@font-face {
  font-family: Porson;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
.greek-text {
  font-family: Porson;
}

.card-modal .card-modal-content .img-block {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-modal.active {
  top: 0;
  transition: top 0ms ease-in-out 0ms;
  z-index: 99999;
}
.card-modal.active .overlay {
  opacity: 1;
  transition: all 300ms ease-in-out;
}
.card-modal.active .card-modal-content {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
}
