.dashboard {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  text-align: center;
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  border-radius: 2rem;
}

.project .imageCropper {
  width: 100px;
  height: 100px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
}

.project img {
  display: inline;
  margin: 0 auto;
  height: 100%;
  width: auto;
}

.link {
  display: flex;
  margin: 2rem 0rem;
  padding: 1rem 5rem;
  align-items: center;
}
.link .icon img {
  position: relative;
  display: block;
  min-width: 60px;
  height: 60px;
  width: 60px;
  line-height: 75px;
  text-align: center;

  padding: 0 15px;
  fill: #658ec6;
}
.link h2 {
  padding: 0rem 2rem;
}
